$font-style: normal;
$font-variant: normal;
$font-weight: normal;
$font-size: 1rem;
$line-height: 1.5;

// font
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;900&display=swap");
$font-family: Nunito, sans-serif;

//color
$color-gray: #7a7a7a;
$color-gray2: #c0c0c0;
$color-gray3: #f2f2f2;
$color-gray4: #585858;
$color-gray5: #bdbdbd;

$color-white: #fff;
$color-black: #000;

$color-red: #f8475f;

$grid-breakpoints: (
  xs: 0,
  cstm: 460px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  io: 1024px,
  xl: 1200px,
) !default;

// Define tipografia
html {
  font: $font-style $font-variant $font-weight #{$font-size}/#{$line-height}
    $font-family;
}

@mixin mq($width, $type: min) {
  @if map_has_key($grid-breakpoints, $width) {
    $width: map_get($grid-breakpoints, $width);
    @if $type == max {
      $width: $width - 1px;
    }
    @media only screen and (#{$type}-width: $width) {
      @content;
    }
  }
}

footer {
  .footer-links {
    background: $color-black;
    .footer-back-links {
      color: $color-white;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      width: 100%;
      max-width: 1248px;
      margin: 0 auto;
      padding: 25px 0;

      @include mq(md, max) {
        & > section {
          padding: 15px;
        }
      }
      @include mq(cstm, max) {
        justify-content: start;
      }

      .address {
        display: flex;
        flex-direction: column;
        gap: 15px;
        h3 {
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 27px;
          &::after {
            content: "";
            border-bottom: 5px solid $color-white;
            width: 44px;
            display: block;
            margin: 15px 0 20px 0;
          }
        }
        p,
        a {
          font-style: normal;
          font-weight: normal;
          font-size: 13px;
          line-height: 12px;
        }
      }

      .contact {
        @include mq(md, max) {
          margin: 0 auto;
        }
        ul {
          display: flex;
          flex-direction: column;
          gap: 25px;
          margin-top: 27px;

          li {
            color: $color-black;
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            width: 195px;
            height: 38px;
            border-radius: 5px;
            background: $color-white;
            a {
              display: flex;
              align-items: center;
              justify-content: space-evenly;
              height: 100%;

              p {
                max-width: 127px;
              }
            }
          }
        }
      }

      .created {
        display: flex;
        gap: 25px;
        align-items: center;
        font-style: normal;
        font-weight: normal;
        font-size: 10.6017px;
        line-height: 14px;

        @include mq(md, max) {
          display: flex;
          justify-content: space-evenly;
          width: 100%;
          padding-bottom: 0;
        }
      }
    }
  }
  .base-footer {
    background: $color-gray2;
    height: 10px;
  }
}

footer {
  .footer-newsletter {
    background: $color-gray3;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 146px;
    gap: 16px;
    @include mq(md, max) {
      height: auto;
      padding: 24px 0;
    }

    h4 {
      font-family: Lato;
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 22px;
      @include mq(md, max) {
        padding: 0 15px;
      }
    }

    #newsletter {
      width: 100%;
      display: flex;
      justify-content: center;

      @include mq(md, max) {
        flex-direction: column;
      }

      .assinar-btn {
        background: $color-black;
        color: $color-white;
        border-radius: 5px;
        padding: 15px;
        width: 140px;
        font-family: Lato;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 14px;
        cursor: pointer;
        @include mq(md, max) {
          margin: 15px auto;
          width: 300px;
        }
      }

      input {
        border: none;
        padding: 15px;
        border-radius: 5px;
        max-width: 248px;
        width: 100%;
        font-family: Lato;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        display: flex;
        align-items: center;
        color: $color-gray4;

        @include mq(md, max) {
          margin: 15px auto;
          max-width: 270px;
        }
      }

      .nwl-email {
        margin: 0 8px;
        @include mq(md, max) {
          margin: 15px auto;
        }
      }
    }

    #success_msg,
    #error_msg {
      display: none;
    }
  }
}

button {
  background: transparent;
  border: none;
  outline: none;
}

html,
body,
h3,
h4,
p {
  margin: 0;
  padding: 0;
  font-family: $font-family;
}

input {
  outline: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

li {
  list-style: none;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
b,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  width: 100%;
  max-width: 1248px;
  margin: 0 auto;

  .menu {
    font-family: $font-family;

    &__mobile {
      @include mq(md, min) {
        display: none;
      }
    }

    &__search {
      width: 100%;
      max-width: 718px;
      position: relative;
      display: flex;
      justify-content: center;

      @include mq(xl, max) {
        max-width: 300px;
      }
      @include mq(md, max) {
        display: none;
      }

      .boxsearch {
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 18px;
        color: $color-gray;
        width: 100%;
        border: none;
        border-bottom: 1px solid $color-gray;
        padding-bottom: 10px;
      }
      .btn-search {
        position: absolute;
        right: 0px;
        cursor: pointer;
      }
    }

    &__user-cart {
      width: 155px;
      height: 20px;
      display: inline-flex;
      justify-content: space-between;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 18px;
      color: $color-gray;
      @include mq(md, max) {
        width: initial;
      }

      .user {
        a {
          @include mq(md, max) {
            display: none;
          }
        }
      }

      .count {
        background: $color-red;
        border-radius: 50%;
        font-style: normal;
        font-weight: bold;
        font-size: 10px;
        line-height: 14px;
        color: $color-white;
        padding: 1px 5px;
      }

      > div a {
        display: flex;
        align-items: center;
        justify-content: space-between;
        white-space: nowrap;
        > img {
          margin-right: 5px;
        }
      }
    }
  }
}

.menu__search-mobile {
  width: 83%;
  position: relative;
  margin: 0 auto 11px;

  @include mq(md, min) {
    display: none;
  }

  .boxsearch {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 18px;
    color: $color-gray;
    width: 100%;
    border: none;
    border-bottom: 1px solid $color-gray;
    padding-bottom: 10px;
  }

  .btn-search {
    position: absolute;
    right: 0px;
  }
}

main {
  .banner {
    img {
      outline: none;
    }

    .slick-dots {
      bottom: 37px;

      li {
        background: $color-gray5;
        border-radius: 50%;
        width: 10px;
        height: 10px;
        margin: 0 9px;

        button:before {
          color: transparent;
          background: transparent;
        }
      }

      .slick-active {
        background: #f8475f;
      }
    }

    @include mq(md, max) {
      &__desktop {
        display: none;
      }
    }

    @include mq(md, min) {
      &__mobile {
        display: none;
      }
    }
  }
}

.shelf {
  max-width: 1161px;
  margin: 25px auto 69px;

  h4 {
    font-family: Nunito;
    font-style: normal;
    font-weight: 900;
    font-size: 18px;
    line-height: 25px;
    color: $color-black;

    &::after {
      display: block;
      content: "";
      background: $color-gray2;
      width: 65px;
      height: 5px;
      margin: 6px 0 20px 0;
    }
  }

  .product {
    text-align: center;
    font-family: Nunito;
    width: 291px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .infos-name {
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: $color-gray;
    }
    .product-image {
      margin-bottom: 8px;
    }
    .product-price {
      .price-list {
        font-family: Nunito;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        text-decoration-line: line-through;
        color: $color-gray;
      }
      .price-principal {
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: $color-black;
      }
      .price-installment {
        font-family: Nunito;
        font-style: normal;
        font-weight: normal;
        font-size: 11px;
        line-height: 15px;
        color: $color-gray;
      }
    }
  }
}
